<template>
  <div>
    <h1>Api using render tepmlate</h1>
    <div>
      <comp ref="comp" v-model="msg"></comp>
      <hr />
      <b style="background: grey; color: red; padding: 2px">{{ msg }}</b>
    </div>
    <hr />
    <div class="status">
      <div class="form-group">
        <h3>Cities Name</h3>
        <ul>
          <transition-group
            name="fade"
            v-on:before-enter="beforeEnter"
            v-on:enter="enter"
            v-on:after-enter="afterEnter"
            v-on:enter-cancelled="enterCancelled"
          >
            <li v-for="elements in list_info" :key="elements">
              {{ elements }}
            </li>
          </transition-group>
        </ul>
        <input class="form-control" v-model="new_item" />
        <hr />
        <div class="d-flex justify-content-evenly">
      <vuebtn @select="add" color="black" :mouseover="red" text="ADD" ><b-icon-arrow-up-circle></b-icon-arrow-up-circle>  </vuebtn>
      <vuebtn @select="remove" color="red"  text="Remove" > </vuebtn>
    </div>
      </div>

    </div>
  </div>
</template>
<script>
import Render2 from "./Small/Render2.vue";
import vueBtn from "./custom/vueBtn.vue";
export default {
  name: "ApiRdr",
  components: {
    comp: Render2,
    "vuebtn": vueBtn
  },
   
  data() {
    return {
      msg: "initial value of msg set in vue data",
      list_info: ["Karachi", "Lahore", "sialkot", "Sakhar", "larkana"],
      new_item: "",
    };
  },
  methods: {
    add: function () {
      if (this.new_item.length) {
        this.list_info.push(this.new_item);
        this.new_item = "";
      }
    },
    remove: function () {
      this.list_info.pop();
    },
    beforeEnter: function () {
      console.log("beforeEnter");
    },
    enter: function () {
      console.log("enter");
    },
    afterEnter: function () {
      console.log("afterEnter");
    },
    enterCancelled: function () {
      console.log("enterCancelled");
    },
  },
};
</script>

<style>
.status {
  background-color: #e219191c;
  height: 300px;
  width: 300px;
  transition: all 2s;
}
.status:hover {
  background-color: rgb(245, 239, 244);
  height: 400px;
  width: 300px;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
  transform: translateY(25px);
}
.fade-enter-active,
.fade-leave-active {
  transition: all 2s;
}
</style>