<template>
  <div>
    <form @submit="submitData" method="post">
      <h1>Sign In Here</h1>
      <input
        type="text"
        name="title"
        v-model="post.title"
        class="form-control"
      /><br />
      <input
        type="text"
        name="auther"
        v-model="post.auther"
        class="form-control"
      /><br />
      <input
        type="password"
        name="password"
        v-model="post.password"
        class="form-control"
      /><br />
      <vueBtn @select="submitData" color="red" text="Post" />
    </form>
  </div>
</template>
<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import vueBtn from "../custom/vueBtn.vue";
Vue.use(VueAxios, axios);
export default {
  name: "singnIn",
  components: {
     vueBtn,
  },
  data() {
    return {
      show: true,
      post: {
        title: null,
        auther: null,
        password: null,
      },
    };
  },
  methods: {
    submitData(e) {
      this.axios.post("");
      alert(
        "Title : " +
          this.post.title +
          "    " +
          "Auther:" +
          this.post.auther +
          "Password" +
          this.post.password
      );
      e.preventDefault();
    },
  },
};
</script>
<style scoped>
.fade-enter {
  opacity: 0;
  font-size: 0px;
}
.fade-enter-active {
  transition: opacity 3s;
  font-size: 3s;
  color: rgb(58, 205, 216);
}
.fade-leave-active {
  transition: opacity 3s;
  font-size: 3s;
  color: rgb(58, 205, 216);
}

.fade-leave-to {
  opacity: 0;
  font-size: 0px;
}
</style>