<template>
  <div>
    <h1>Inject method in API</h1>
    <p>{{ msg1 }}</p>
    <p>{{ msg2 }}</p>
    <p>{{ msg3 }}</p>
    <hr />
    <comp
      ref="comp"
      :prop1="first"
      :prop2="second"
      :prop3="third"
      :prop4="fourth"
    >
    </comp>
    <hr>
    <h2>This is render Array</h2>
    <reder>
    </reder>
  </div>
</template>
<script>
import ApiChild from "./Small/ApiChild.vue";
import Render from './Small/Render.vue';

export default {
  name: "Api",
  components: {
    "comp": ApiChild,
    "reder":Render

  },
    props:['prop1','prop2','prop3','prop4'],

  data() {
    return {
      msg1: "Vue Data Massage 1",
      msg2: "Vue Data Massage 2",
      msg3: "Vue Data Massage 3",
    };
  },
  methods: {
    display: function () {
      alert(this.$props.msg);
    },
  },
  provide: {
    msg: "Msg define in Parente Component",
  },
};
</script>