<template>
  <div>
    <form @submit="submitData" method="post">
      <h1>Sign Up Here</h1>
      <input
        type="text"
        name="title"
        v-model="post.title"
        class="form-control"
      /><br />
      <input
        type="text"
        name="auther"
        v-model="post.auther"
        class="form-control"
      /><br />
      <input
        type="password"
        name="password"
        v-model="post.password"
        class="form-control"
      /><br /> <input
        type="password"
        name="repassword"
        v-model="post.repassword"
        class="form-control"
      /><br />
      <vueBtn @select="submitData" color="blue" text="Post" />

    </form>
  </div>
</template>
<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import vueBtn from '../custom/vueBtn.vue';

Vue.use(VueAxios, axios);
export default {
     name: "singnUp",
     components:{
      vueBtn,
     },
  data() {
    return {
      show: true,
      post: {
        title: null,
        auther: null,
        password: null,
        repassword:null,
      },
    };
  },
  methods: {  
    submitData(e) {
      this.axios.post("");
      alert(
        "Title : " +
          this.post.title +
          "    " +
          "Auther:" +
          this.post.auther +
          "Password" +
          this.post.repassword
        
      );
      e.preventDefault();
    },
  },
}
</script>