<script>
export default {
  render: function (createElement) {
    var h = createElement(
      "h2",
      {
        style: {
          fontFamily: "helvitica",
          background: "grey",
          color: "white",
        },
      },
      "Second arguments to with Multiple h2 Tag Array"
    );
    var p = createElement(
      "p",
      {
        style: {
          fontFamily: "helvitica",
          background: "cyan",
          color: "black",
        },
      },
      "Second argument of render elements as P tag"
    );
    return createElement("div", [h,p,p,p ]);
  },
};
</script>