import Vue from 'vue'
import App from './App.vue'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import BootstrapVueIcons from 'bootstrap-vue'
import VueRouter from 'vue-router'
import VueLazyload from 'vue-lazyload'
import vueImages from 'vue-images'
import Block from './components/Block'
import User from './components/User'
import Api from './components/Api'
import Form from './components/Form'
import Login from './components/Login'
import ApiRdr from './components/ApiRdr'
import Monday from './components/Monday'
import Tuesday from './components/Tuesday'
import vueBtn from './components/custom/vueBtn'
import prtCom from './components/prtCom'
// import vueImages from 'vue-images'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

// require('vue-images/dist/vue-images.css')
// Make BootstrapVue available throughout your project
Vue.use(BootstrapVue)
Vue.use(vueImages)
Vue.use(VueLazyload)
Vue.use(BootstrapVueIcons)
Vue.use(vueBtn)
Vue.use(IconsPlugin)
Vue.directive("size", {
  bind(el, binding) {
    if (binding.value == 'small') {
      el.style.fontSize = "20px"
    }
    else if (binding.value == 'big') {
      el.style.fontSize = "80px",
        el.style.background = "grey",
        el.style.color = "white"
    }
    else if (binding.value == 'red') {
      el.style.fontSize = "20px",
        el.style.background = "red",
        el.style.color = "white"
    }
    else {
      el.style.fontSize = "40px"
      el.style.background = "Black"
      el.style.color = "white"
    }
  }
})
Vue.directive('test', {
  bind: function (el) {
    console.log(el.parentNode)
  },
  inserted: function () {
    console.log("I am a Inserted Hook")

  },
  unbind: function () {
    console.log("I am a unbind Hook")
  },
  update: function (el, binding) {
    console.log('name :' + binding.name)
    console.log('vale :' + binding.value)
    console.log('oldValue :' + binding.oldValue)
    console.log('expression :' + binding.expression)
    console.log('arg :' + binding.arg)
    console.log('modifiers :' + binding.modifiers)
    console.log("UPDATE:" + el.innerText)
  },
  componentUpdated: function (el) {
    console.log("componentUpdated:" + el.innerText)
  },

})
Vue.mixin({
  data(){
    return{
      mixin_msg:"this is Global Mixin"
    }
  }
})

Vue.config.productionTip = false
Vue.use(VueRouter);
const routes = [
  { path: '/block', component: Block },
  { path: '/monday', component: Monday },
  { path: '/news', component: Tuesday },
  { path: '/login', component: Login },
  { path: '/apirdr', component: ApiRdr },
  { path: '/user', component: User },
  { path: '/api', component: Api },
  { path: '/prtcom', component: prtCom },
  { path: '/form', component: Form },



]

const router = new VueRouter({
  routes

})
new Vue({
  router: router,
  render: h => h(App),

}).$mount('#app')
