<template>
  <div>
    <h1 v-size="'big'">Custom Directive in Vue JS Big</h1>
    <h1 v-size="'medim'">Custom Directive With binding</h1>
    <h1 v-size="'small'">Custom Directive in Vue JS With binding</h1>
    <p v-if="show" v-test>I am Paragraph</p>
    <vueBtn
      @select="show = !show"
      color="black"
      :mouseover="red"
      text="Show Hide"
    />
    <p v-test>
      The Number Is : <b v-size="'red'"> {{ number }}</b>
    </p>
    <p v-size="'red'" v-test:argument.modifire="msg">{{ msg }}</p>
    <div class="d-flex justify-content-evenly">
      <vueBtn @select="add" color="green" :mouseover="red" text="Add one" />
      <vueBtn @select="update" color="blue" :mouseover="red" text="Update" />
    </div>
    <p v-for="element in $data" :key="element" v-size="'red'">{{ element }}</p>
  </div>
</template>
<script>
import vueBtn from "./custom/vueBtn.vue";
var my_mixin = {
  template: +"<b> Template by Mixixn</b>",

  data() {
    return {
      // mixin_msg: "MSG Define in Mixin",
      // msg1: "MSG Define in Mixin 2",
    };
  },

  methods: {
    sayBi: function () {
      alert("Say by to You By Mixin Method");
    },
    sayHi: function () {
      alert("Say HI to You By Mixin Method");
    },
  },
  created: function () {
    alert(" Mixin Created Hook");
  },
};
export default {
  name: "Monday",
  components: {
    vueBtn,
  },
  mixins: [my_mixin],
  data() {
    return {
      show: true,
      number: 1,
      msg: "Message Define in VUE Data",
    };
  },
  methods: {
    add: function () {
      this.number++;
    },
    update: function () {
      this.msg = "Updated Message Value";
    },
    sayHi: function () {
      alert("Say HI to You By VUE Method");
    },
  },
  created: function () {
    alert(" VUE Created Hook");
  },
};
</script>