<template>
  <div>
    <input
      @click="clickEvent"
      @mouseover="actionhover"
      class="mybtn"
      type="button"
      :value="text"
      :style="mystyle"
    />
  </div>
</template>
<script>
export default {
  name: "vueBtn",
  data() {
    return {
      bgColor: "#ccc"
    };
  },
  props: ["text", "color", "click", "mousedown"],
  computed: {
    mystyle() {
      return {
        border: "2px solid " + this.color,
        color: this.color,
      };
    },
  },
  methods: {
    clickEvent() {
      // console.log("clickevent from VueBtn")
      this.$emit("select");
    },
    actionhover() {
      // this.$emit("background");
      console.log("click background");
      this.bgColor="red"
 
 },
  },
};
</script>
<style scoped>
.mybtn {
  min-width: 100px;
  width: max-content;
  height: 40px;
  border-radius: 30px 0;
  background: transparent;

  font-size: 18px;
}
.mybtn:hover {
  /* min-width: 100px;
  width: max-content;
  height: 60px;
  border-radius: 50px 0;
  font-size: 38px; */
  /* background: brown; */
  color: white !important;
}
</style>